header {
    .intro {
        display: flex;
        justify-content: center;
        align-items: center;
        height: 100vh;
        height: 100svh;

        .introLogo {
            position: relative;
            height: 55%;

            &::before {
                content: "";
                position: absolute;
                inset: -5%;
                display: block;
                box-shadow: 0 0 5vmax var(--accent), 0 0 10vmax var(--accent),
                    0 0 15vmax var(--paleAccent), 0 0 20vmax var(--paleAccent),
                    0 0 25vmax var(--paleAccent);
                z-index: -3;
                border-radius: 50%;
            }

            svg {
                path {
                    transition: transform 1.3s cubic-bezier(0.165, 0.84, 0.44, 1);

                    &:nth-child(1) {
                        fill: var(--secAccent);
                    }

                    &:nth-child(2) {
                        fill: var(--secColor);
                    }

                    &:nth-child(3) {
                        fill: var(--tertAccent);
                    }

                    &:last-child {
                        fill: var(--accent);
                    }
                }

                height: 100%;
            }
        }

        .introName {
            display: flex;
            pointer-events: none;

            .introTitle {
                display: flex;
                flex-direction: column;

                h1 {
                    color: var(--accent);
                    text-shadow: 0.5rem 0.3rem 0 var(--tertAccent),
                        1rem 0.5rem 0 var(--secAccent);
                    font-size: 15rem;
                    line-height: 11rem;
                    letter-spacing: -0.25rem;
                    font-family: antique-olive, -apple-system, BlinkMacSystemFont,
                        Segoe UI, Roboto, Helvetica Neue, Arial, Noto Sans, sans-serif,
                        Apple Color Emoji, Segoe UI Emoji, Segoe UI Symbol, Noto Color Emoji;
                    text-transform: lowercase;
                }
            }

            .introSubTitle {
                font-family: Matter, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto,
                    Helvetica Neue, Arial, Noto Sans, sans-serif, Apple Color Emoji,
                    Segoe UI Emoji, Segoe UI Symbol, Noto Color Emoji;
                text-transform: uppercase;
                letter-spacing: 0.26rem;
                color: var(--tpPri);
                text-shadow: 0.1rem 0.1rem 0 var(--secColor);

                font-size: large;

                display: flex;
                flex-direction: column;

                position: relative;
                left: -7rem;
                top: 9rem;
            }
        }
    }

    .about {
        height: 100vh;
        height: 100svh;
        display: flex;
        justify-content: center;

        .aboutTitle {
            font-family: Matter, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Helvetica Neue, Arial, Noto Sans, sans-serif, Apple Color Emoji, Segoe UI Emoji, Segoe UI Symbol, Noto Color Emoji;
            font-size: large;
            font-weight: 650;
            text-transform: uppercase;
            letter-spacing: .2em;
            color: #a374ff;
            text-indent: .5rem;
        }

        .aboutInfo {
            max-width: 55%;
            border-radius: 0.5rem;
            padding-block: 2rem;

            display: flex;
            flex-direction: column;
            justify-content: space-around;
            gap: 5%;
            height: 100%;

            div {
                transition: transform 1s cubic-bezier(0.19, 0.8, 0.22, 1.05),
                    opacity 300ms ease;

                &.invisible {
                    transform: translateX(-70%);
                    opacity: 0;
                }

                h1 {
                    color: var(--secAccent);
                    text-shadow: 0.15rem 0.15rem 0 var(--accent);
                    font-size: 4.5rem;
                    line-height: 4rem;
                    letter-spacing: -0.25rem;
                    font-family: antique-olive, -apple-system, BlinkMacSystemFont,
                        Segoe UI, Roboto, Helvetica Neue, Arial, Noto Sans, sans-serif,
                        Apple Color Emoji, Segoe UI Emoji, Segoe UI Symbol, Noto Color Emoji;
                    text-transform: lowercase;
                }

                p {
                    font-family: Matter, -apple-system, BlinkMacSystemFont, Segoe UI,
                        Roboto, Helvetica Neue, Arial, Noto Sans, sans-serif,
                        Apple Color Emoji, Segoe UI Emoji, Segoe UI Symbol, Noto Color Emoji;
                    letter-spacing: 0.27rem;
                    color: var(--tpPri);
                    text-shadow: 0.05rem 0.05rem 0 var(--secColor);
                    font-size: 1.3rem;
                    margin-top: 1rem;
                    margin-left: 1rem;

                    span {
                        font-weight: 600;
                        letter-spacing: 0.2rem;
                        color: var(--accent);
                        text-shadow: 0.07rem 0.07rem 0 var(--secAccent);
                    }
                }

                &:nth-child(3) {
                    max-width: 80%;

                    h1 {
                        color: var(--accent);
                        text-shadow: 0.15rem 0.15rem 0 var(--secAccent);
                    }

                    p {
                        span {
                            color: var(--secAccent);
                            text-shadow: 0.07rem 0.07rem 0 var(--accent);
                        }
                    }
                }

                &:last-child {
                    h1 {
                        color: var(--tertAccent);
                        text-shadow: 0.15rem 0.15rem 0 var(--additionalAccent);
                    }

                    p {
                        span {
                            color: var(--additionalAccent);
                            text-shadow: 0.07rem 0.07rem 0 var(--tertAccent);
                        }
                    }
                }
            }
        }

        .skills {
            display: flex;
            align-items: center;
            justify-content: center;
            flex-direction: column;

            .skillsGrid {
                flex: 0 0 auto;
                perspective: 60rem;
                display: grid;
                grid-template-columns: repeat(3, 10rem);
                grid-template-rows: repeat(3, 10rem);
                gap: 2rem;
                transform: rotate(45deg);

                .invisible {
                    transform: translateY(-60%) rotate(-65deg);
                    opacity: 0;
                }

                div {
                    transition: transform 0.7s cubic-bezier(0.25, 0.1, 0.25, 1),
                        opacity 300ms ease;
                    background-color: var(--tpPri);
                    display: flex;
                    justify-content: center;
                    align-items: center;

                    border-radius: 0.5rem;

                    section {
                        width: 85%;
                        height: 85%;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        border-radius: inherit;
                        background-color: var(--extraPrimary);

                        svg {
                            height: 4rem;
                            width: 4rem;
                            transition: transform 0.7s cubic-bezier(0.3, 0.84, 0.44, 1.4);
                        }
                    }

                    &:hover {
                        transform: translate3d(0, 0, 5rem) scale(1.2);

                        section {
                            svg {
                                transform: rotate(-45deg);
                            }
                        }
                    }

                    &:first-child {
                        background: radial-gradient(circle at left top,
                                transparent 0%,
                                rgba(0, 0, 0, 0),
                                var(--paleAccent) 85%),
                            radial-gradient(circle at right bottom,
                                transparent 0%,
                                rgba(0, 0, 0, 0),
                                var(--paleSecAccent) 85%);
                    }

                    &:nth-child(2) {
                        background: radial-gradient(circle at left top,
                                transparent 0%,
                                rgba(0, 0, 0, 0),
                                var(--paleTertAccent) 85%),
                            radial-gradient(circle at right bottom,
                                transparent 0%,
                                rgba(0, 0, 0, 0),
                                var(--paleAccent) 85%);
                    }

                    &:nth-child(3) {
                        background: radial-gradient(circle at left top,
                                transparent 0%,
                                rgba(0, 0, 0, 0),
                                var(--additionalPaleAccent) 85%),
                            radial-gradient(circle at right bottom,
                                transparent 0%,
                                rgba(0, 0, 0, 0),
                                var(--paleTertAccent) 85%);

                        svg {
                            width: 5rem;
                            height: 5rem;
                        }
                    }

                    &:nth-child(4) {
                        background: radial-gradient(circle at left top,
                                transparent 0%,
                                rgba(0, 0, 0, 0),
                                var(--paleAccent) 85%),
                            radial-gradient(circle at right bottom,
                                transparent 0%,
                                rgba(0, 0, 0, 0),
                                var(--paleSecAccent) 85%);
                    }

                    &:nth-child(5) {
                        background: radial-gradient(circle at left top,
                                transparent 0%,
                                rgba(0, 0, 0, 0),
                                var(--paleTertAccent) 85%),
                            radial-gradient(circle at right bottom,
                                transparent 0%,
                                rgba(0, 0, 0, 0),
                                var(--paleAccent) 85%);

                        svg {
                            width: 5rem;
                            height: 5rem;
                        }
                    }

                    &:nth-child(6) {
                        background: radial-gradient(circle at left top,
                                transparent 0%,
                                rgba(0, 0, 0, 0),
                                var(--additionalPaleAccent) 85%),
                            radial-gradient(circle at right bottom,
                                transparent 0%,
                                rgba(0, 0, 0, 0),
                                var(--paleTertAccent) 85%);

                        svg {
                            width: 5rem;
                            height: 5rem;
                        }
                    }

                    &:nth-child(7) {
                        background: radial-gradient(circle at left top,
                                transparent 0%,
                                rgba(0, 0, 0, 0),
                                var(--paleAccent) 85%),
                            radial-gradient(circle at right bottom,
                                transparent 0%,
                                rgba(0, 0, 0, 0),
                                var(--paleSecAccent) 85%);
                    }

                    &:nth-child(8) {
                        background: radial-gradient(circle at left top,
                                transparent 0%,
                                rgba(0, 0, 0, 0),
                                var(--paleTertAccent) 85%),
                            radial-gradient(circle at right bottom,
                                transparent 0%,
                                rgba(0, 0, 0, 0),
                                var(--paleAccent) 85%);
                    }

                    &:last-child {
                        background: radial-gradient(circle at left top,
                                transparent 0%,
                                rgba(0, 0, 0, 0),
                                var(--additionalPaleAccent) 85%),
                            radial-gradient(circle at right bottom,
                                transparent 0%,
                                rgba(0, 0, 0, 0),
                                var(--paleTertAccent) 85%);
                    }
                }
            }
        }
    }
}

@media (max-width: 1600px) {
    header {
        .about {
            .aboutInfo {
                padding-block: 2rem;
                gap: 3%;

                div {
                    h1 {
                        text-shadow: 0.1rem 0.1rem 0 var(--accent);
                        font-size: 3.5rem;
                        line-height: 2.5rem;
                        letter-spacing: -0.2rem;
                    }

                    p {
                        letter-spacing: 0.23rem;
                        font-size: 1.1rem;
                        margin-left: 1rem;
                    }
                }
            }

            .skills {
                .skillsGrid {
                    gap: 1.5rem;
                    grid-template-columns: repeat(3, 9rem);
                    grid-template-rows: repeat(3, 9rem);

                    div {
                        section {
                            svg {
                                width: 3rem;
                                height: 3rem;
                            }
                        }

                        &:hover {
                            transform: translate3d(0, 0, 4rem) scale(1.1);
                        }

                        &:nth-child(3),
                        &:nth-child(5),
                        &:nth-child(6) {
                            svg {
                                width: 4rem;
                                height: 4rem;
                            }
                        }
                    }
                }
            }
        }
    }
}

@media (max-width: 1400px) {
    header {
        .about {
            .aboutInfo {
                gap: 2%;

                div {
                    h1 {
                        font-size: 3rem;
                        line-height: 2rem;
                    }

                    p {
                        font-size: 1rem;
                        margin-left: 0.7rem;
                    }
                }
            }

            .skills {
                .skillsGrid {
                    gap: 1rem;
                    grid-template-columns: repeat(3, 7.5rem);
                    grid-template-rows: repeat(3, 7.5rem);

                    div {
                        section {
                            svg {
                                width: 2.5rem;
                                height: 2.5rem;
                            }
                        }

                        &:hover {
                            transform: translate3d(0, 0, 3.5rem) scale(1.1);
                        }

                        &:nth-child(3),
                        &:nth-child(5),
                        &:nth-child(6) {
                            svg {
                                width: 3.5rem;
                                height: 3.5rem;
                            }
                        }
                    }
                }
            }
        }
    }
}

@media (max-width: 1200px) {
    header {
        .intro {
            .introLogo {
                height: 45%;

                &::before {
                    content: "";
                    position: absolute;
                    inset: -5%;
                    display: block;
                    box-shadow: 0 0 5vmax var(--accent), 0 0 10vmax var(--accent),
                        0 0 15vmax var(--paleAccent), 0 0 20vmax var(--paleAccent),
                        0 0 25vmax var(--paleAccent);
                    z-index: -3;
                    border-radius: 50%;
                }
            }

            .introName {
                .introTitle {
                    h1 {
                        text-shadow: 0.3rem 0.3rem 0 var(--tertAccent),
                            0.5rem 0.5rem 0 var(--secAccent);
                        font-size: 10rem;
                        line-height: 7.5rem;
                        letter-spacing: -0.1rem;
                    }
                }

                .introSubTitle {
                    letter-spacing: 0.26rem;
                    text-shadow: 0.1rem 0.1rem 0 var(--secColor);
                    font-size: medium;
                    left: -5rem;
                    top: 5.7rem;
                }
            }
        }

        .about {
            margin-block: 5rem;
            height: unset;
            padding: 0;
            justify-content: center;

            .aboutInfo {
                padding: 0;
                gap: 1.5rem;

                div {
                    h1 {
                        font-size: 2.5rem;
                        line-height: 1.5rem;
                        letter-spacing: -0.1rem;
                    }

                    p {
                        font-size: 0.9rem;
                        margin-left: 0.6rem;
                    }
                }
            }

            .skills {
                .skillsGrid {
                    grid-template-columns: repeat(3, 6.5rem);
                    grid-template-rows: repeat(3, 6.5rem);

                    div {
                        section {
                            svg {
                                width: 2rem;
                                height: 2rem;
                            }
                        }

                        &:hover {
                            transform: translate3d(0, 0, 4rem) scale(1.1);

                            section {
                                svg {
                                    transform: rotate(-45deg);
                                }
                            }
                        }

                        &:nth-child(3),
                        &:nth-child(5),
                        &:nth-child(6) {
                            svg {
                                width: 3rem;
                                height: 3rem;
                            }
                        }
                    }
                }
            }
        }
    }
}

@media (max-width: 1100px) {
    header {
        .about {
            padding: 0;
            justify-content: center;
            width: 100%;

            .aboutTitle {
                font-size: small;
                letter-spacing: .03rem;
                text-indent: 0.1rem;
            }

            .aboutInfo {
                max-width: unset;
                width: fit-content;
                gap: 1.5rem;
                padding-inline: 1rem;

                div {
                    h1 {
                        font-size: 2rem;
                        line-height: 2rem;
                        letter-spacing: -0.1rem;
                    }

                    p {
                        font-size: 0.85rem;
                        letter-spacing: 0rem;
                        margin-left: 0.3rem;
                        font-weight: 600;
                        text-shadow: 0.03rem 0.03rem 0 var(--secColor);
                        color: var(--tpPri);

                        span {
                            font-weight: 700;
                            letter-spacing: 0.1rem;
                            color: var(--accent);
                            text-shadow: 0.03rem 0.03rem 0 var(--secAccent);
                        }
                    }

                    &:nth-child(3) {
                        max-width: unset;

                        h1 {
                            text-shadow: 0.1rem 0.1rem 0 var(--secAccent);
                        }

                        p {
                            span {
                                text-shadow: 0.03rem 0.03rem 0 var(--accent);
                            }
                        }
                    }

                    &:last-child {
                        h1 {
                            text-shadow: 0.1rem 0.1rem 0 var(--additionalAccent);
                        }

                        p {
                            span {
                                text-shadow: 0.03rem 0.03rem 0 var(--tertAccent);
                            }
                        }
                    }
                }
            }

            .skills {
                display: none;
            }
        }
    }
}

@media (max-width: 800px) {
    header {
        .intro {
            flex-direction: column;
            justify-content: center;
            align-items: center;
            background: none !important;

            $firstValue: 70svh;
            $secondValue: 30rem;

            height: max($firstValue, $secondValue);

            .introLogo {
                $firstLogoValue: 40%;
                $secondLogoValue: 10rem;

                height: max($firstLogoValue, $secondLogoValue);

                &::before {
                    content: "";
                    position: absolute;
                    inset: -5%;
                    display: block;
                    box-shadow: 0 0 5vmax var(--accent), 0 0 10vmax var(--accent),
                        0 0 15vmax var(--paleAccent), 0 0 20vmax var(--paleAccent),
                        0 0 25vmax var(--paleAccent);
                    z-index: -3;
                    border-radius: 50%;
                }

                svg {
                    path {
                        &:nth-child(1) {
                            translate: 0% -0.5%;
                        }

                        &:nth-child(2) {
                            translate: 2% -3.5%;
                        }

                        &:nth-child(3) {
                            translate: 4% -7.5%;
                        }

                        &:last-child {
                            translate: 6% -10.5%;
                        }
                    }
                }
            }

            .introName {
                position: relative;
                left: 1.5rem;

                .introTitle {
                    h1 {
                        text-shadow: 0.2rem 0.2rem 0 var(--tertAccent),
                            0.4rem 0.4rem 0 var(--secAccent);
                        font-size: 7rem;
                        line-height: 5.5rem;
                    }
                }

                .introSubTitle {
                    font-size: small;
                    left: -3rem;
                    top: 2rem;
                }
            }
        }
    }
}